.App {
  text-align: center;
  display: flex;
  height: 100%;
  color: white;
}

.leftList {
  background-color: #020428;
  margin: 1vh;
  height: 98vh; /* 98 so that the 1vh margins dont make it scroll */
  width: 20vh;
}

.middleList {
  background-color: #020428;
  margin: 1vh;
  height: 98vh; /* 98 so that the 1vh margins dont make it scroll */
  width: 70vh;
  overflow-y: scroll;
}

.rightList {
  display: flex;
  background-color: #020428;
  height: 98vh; /* 98 so that the 1vh margins dont make it scroll */
  width: 100%;
  margin: 1vh;
  align-items: center;
  justify-content: center;
}

